// 1.  "xxxxxx" 
/**
 * @param {Date|string|number} dateInput
 * @returns {string}
 */
export function formatToChineseYearMonth(dateInput) {
  // 1.  null, undefined,  (0)
  if (!dateInput && typeof dateInput !== 'number') {
    return ''; 
  }

  // 2.  "xxxxxx" 
  if (typeof dateInput === 'string' && /^\d{4}\d{2}$/.test(dateInput)) {
    return dateInput;
  }

  let dateObj;

  if (dateInput instanceof Date) {
    dateObj = dateInput; // 3. Date
  } else {
    // 4.  Date 
    let parsableDateInput = dateInput;
    //  "YYYY-MM" /
    if (typeof dateInput === 'string' && /^\d{4}-\d{2}$/.test(dateInput)) {
      //  "YYYY-MM"  "YYYY-MM-01T00:00:00Z"UTC
      //  new Date() 
      parsableDateInput = `${dateInput}-01T00:00:00.000Z`; //  .000Z 
    }
    dateObj = new Date(parsableDateInput);
  }

  // 5.  dateObj 
  if (dateObj instanceof Date && !isNaN(dateObj.getTime())) {
    let year;
    let month;

    //  "YYYY-MM" UTC
    // UTC
    if (typeof dateInput === 'string' && /^\d{4}-\d{2}$/.test(dateInput)) {
      year = dateObj.getUTCFullYear();
      month = String(dateObj.getUTCMonth() + 1).padStart(2, '0'); // getUTCMonth()  0-11
    } else {
      // Date
      // 
      year = dateObj.getFullYear();
      month = String(dateObj.getMonth() + 1).padStart(2, '0'); // getMonth()  0-11
    }

    return `${year}${month}`;
  }

  // 6. 
  console.warn('[formatToChineseYearMonth] :', dateInput);
  return String(dateInput); 
}

// 2.  "YYYY-MM-DD"
export function formatToYYYYMMDD(isoString) {
  if (!isoString) return '';
  try {
    const date = new Date(isoString);
    if (isNaN(date.getFullYear())) {
        return isoString; 
    }
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
  } catch (e) {
    console.error('Error formatting date to YYYY-MM-DD:', isoString, e);
    return isoString; 
  }
}

// 3. 2
/**
 * @param {number|string} value - 
 * @param {number} decimalPlaces -  2
 * @returns {string} - '-'
 */
export function formatNumber(value, decimalPlaces = 2) {
  // 1.  null, undefined, 
  if (value === null || value === undefined || String(value).trim() === '') {
    return '-'; //  '-'
  }

  // 2. 
  const stringValue = String(value).replace(/,/g, '');
  const num = parseFloat(stringValue);

  // 3. 
  if (isNaN(num)) {
    console.warn('[formatNumberWithSeparators] :', value);
    return String(value); // 
  }

  // 4.  toLocaleString 
  // toLocaleString 
  // undefined 
  return num.toLocaleString(undefined, {
    minimumFractionDigits: decimalPlaces,
    maximumFractionDigits: decimalPlaces,
  });
}

// 4. 1
/**
 * @param {number|string} value
 * @param {number} decimalPlaces
 * @returns {string}
 */
export function formatPercentage(value, decimalPlaces = 1) {
  // 1.  null, undefined, 
  if (value === null || value === undefined || String(value).trim() === '') {
    return '-'; //  '-'
  }

  // 2. 
  const stringValue = String(value).replace(/%/g, '');
  const num = parseFloat(stringValue);

  // 3. 
  if (isNaN(num)) {
    console.warn('[formatAsPercentage] :', value);
    return String(value); // 
  }

  // 4. 100 '%' 
  return (num * 100).toFixed(decimalPlaces) + '%';
}

export function formatToDateTimeSec(isoString) {
  if (!isoString) return ''; // 
  try {
    const date = new Date(isoString);
    if (isNaN(date.getTime())) return isoString; // 

    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    const hours = String(date.getHours()).padStart(2, '0');
    const minutes = String(date.getMinutes()).padStart(2, '0');
    const seconds = String(date.getSeconds()).padStart(2, '0');

    return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
  } catch (e) {
    console.error('Error formatting date to YYYY-MM-DD HH:MM:SS:', isoString, e);
    return isoString; // 
  }
}