<template>
  <div class="chart-wrapper" style="position: relative;">
    <div class="chart-title">{{ chartTitle }}</div>
    <ChartSpinner :visible="loading" />
    <v-chart
      v-if="!loading && chartOption.series && chartOption.series.length"
      :option="chartOption"
      style="width: 100%; height: 440px;"
    />
  </div>
</template>

<script>
import { defineComponent, ref, watch } from 'vue';
import VChart from 'vue-echarts';
import ChartSpinner from '@/components/common/ChartSpinner.vue';
import chartColors from '@/utils/chartColors.js';

export default defineComponent({
  name: 'ComboBarLineChart',
  components: {
    VChart,
    ChartSpinner
  },
  props: {
    chartTitle: {
      type: String,
      default: ' + '
    },
    categories: {
      type: Array,
      default: () => []  //  
    },
    barSeries: {
      type: Array,
      default: () => []  //  
    },
    lineSeries: {
      type: Object,
      default: () => ({ name: '', data: [] }) //  
    },
    yAxisBarName: {
      type: String,
      default: ''
    },
    yAxisLineName: {
      type: String,
      default: ''
    }
  },
  setup(props) {
    const chartOption = ref({});
    const loading = ref(false);

    const updateChart = () => {
      loading.value = true;
      setTimeout(() => {
        const barLabelOption = {
          show: true,         // 
          position: 'top',    // 'top'
          formatter: '{c}',
          fontSize: 12,       // 
          color: '#005f73',   // 
          // distance: 5,     // 
        };

        const bar = (props.barSeries || []).map((s, idx) => ({
          name: s.name,
          type: 'bar',
          data: s.data || [],
          yAxisIndex: 0,
          itemStyle: {
            color: chartColors[idx % chartColors.length]
          },
          label: barLabelOption //  barlabel 
        }));

        const customLineColor = '#ee9b00';
        const actualLineSeriesName = props.lineSeries?.name || '';

        const lineLabelOption = {
          show: true,         // 
          position: 'top',    // 'top'
          formatter: function (params) {
            //  null/undefined 
            if (params.value == null || isNaN(parseFloat(params.value))) { 
              return ''; // 
            }
            // 
            const value = parseFloat(params.value);
            return value.toFixed(2); // 
          }, // <---  ,
          fontSize: 12,       // 
          color: '#ee9b00',   // 
          // distance: 5,     // 
        };

        const line = {
          name: actualLineSeriesName, // 
          type: 'line',
          data: props.lineSeries?.data || [],
          yAxisIndex: 1,
          symbol: 'circle',
          lineStyle: {
            width: 3,
            color: customLineColor 
          },
          itemStyle: {
            color: customLineColor
          },
          label: lineLabelOption
        };

        chartOption.value = {
          color: chartColors,
          tooltip: {
            trigger: 'axis',
            axisPointer: { type: 'shadow' },
            formatter: function (params) {
              let tooltipContent = '';
              if (params && params.length > 0) {
                // params[0].name X ( 'Q1')
                // params[0].axisValueLabel X
                tooltipContent += (params[0].axisValueLabel || params[0].name) + '<br />'; 
                
                params.forEach(param => {
                  // param 
                  // param.seriesName  ( "", "")
                  // param.value 
                  // param.marker HTML

                  let valueToShow = param.value; // 

                  //  ()
                  if (param.seriesName === actualLineSeriesName) {
                    const num = parseFloat(param.value);
                    if (!isNaN(num)) {
                      valueToShow = num.toFixed(2); // 
                    } else if (param.value == null) {
                        valueToShow = '-'; //  null  undefined
                    } else {
                        valueToShow = String(param.value); // 
                    }
                  } else {
                    // 
                    const num = parseFloat(param.value);
                    if (!isNaN(num)) {
                      valueToShow = num.toFixed(0); // 
                    } else if (param.value == null) {
                        valueToShow = '';
                    } else {
                        valueToShow = String(param.value);
                    }
                  }
                  tooltipContent += param.marker + ' ' + param.seriesName + ': <b>' + valueToShow + '</b><br />';
                });
              }
              return tooltipContent;
            }
          },
          legend: { top: 10 },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'category',
            data: props.categories || [],
            axisLabel: {
              fontSize: 11,
              rotate: 0,
              interval: 0,
              formatter: val => (val.length > 6 ? val.slice(0, 6) + '' : val)
            }
          },
          yAxis: [
            {
              type: 'value',
              name: props.yAxisBarName,
              min: 0,                  //  0
              max: 120,                //  100
              interval: 20,            //  20
              nameLocation: 'middle',
              nameGap: 45,
              nameTextStyle: { fontSize: 14 }
            },
            {
              type: 'value',
              name: props.yAxisLineName,
              min: 0,                  //  0
              max: 24,                //  100
              interval: 4,            //  20
              nameLocation: 'middle',
              nameGap: 45,
              nameTextStyle: { fontSize: 14 }
            }
          ],
          series: [...bar, line]
        };

        loading.value = false;
      }, 500);
    };

    watch(
      () => [props.barSeries, props.lineSeries, props.categories],
      updateChart,
      { immediate: true, deep: true }
    );

    return {
      chartOption,
      loading
    };
  }
});
</script>

<style scoped>
.chart-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  width: 100%;
  min-width: 0px;
}

.chart-title {
  font-size: 18px;
  font-weight: bold;
  margin: 20px 0;
  text-align: center;
  color: #003049;
}
</style>
