<template>
  <div class="page-container">
    <LoadingSpinner :visible="isLoading" />

    <div class="header">
      <div class="header-logos">
        <img src="@/assets/logo/SAIF LOGO.jpg" alt="SAIF Logo" />
        <img src="../assets/logo/CAFR.png" alt="CAFR Logo" />
        <img src="../assets/logo/.png" alt="Lab Logo" />
      </div>

      <img class="header-bg-image" src="../assets/header_image.jpg" alt="Header Image" />
      
      <div class="header-content">
        <div class="left">
          <h1><br /></h1>
        </div>
      </div>
    </div>

    <div v-if="isLoginModalVisible" class="login-modal">
      <div class="modal-content">
        <LoginForm @login-success="handleLoginSuccess" />
        <button class="login-close-button" @click="closeLoginModal"></button>
      </div>
    </div>

    <div v-if="isFeedbackModalVisible" class="feedback-modal">
      <div class="feedback-modal-content">
        <h2> / </h2>
        <form @submit.prevent="submitFeedback" class="feedback-form">
          <div class="form-group">
            <label for="name"></label>
            <input type="text" id="name" v-model="feedbackForm.name" required />
          </div>
          <div class="form-group">
            <label for="contact"></label>
            <input type="text" id="contact" v-model="feedbackForm.contact" required />
          </div>
          <div class="form-group">
            <label for="organization"></label>
            <input type="text" id="organization" v-model="feedbackForm.organization" />
          </div>
          <div class="form-group">
            <label for="details">/</label>
            <textarea id="details" v-model="feedbackForm.details" rows="6" required></textarea>
          </div>
          <div class="form-buttons">
            <button type="submit" class="submit-button"></button>
            <button type="button" class="close-button" @click="closeFeedbackModal"></button>
          </div>
        </form>
      </div>
    </div>

    <div class="status-row">
      <div class="status-box clickable" @click="handleStatusBoxClick('')">
        <h3>2024</h3>
        <p class="status-title"></p>
        <p class="status-number">
          {{ listedCompanyCount }} 
        </p>
        <div class="status-note"></div>
      </div>

      <div class="status-box clickable" @click="handleStatusBoxClick('')">
        <h3>{{ nonListedMonthDisplay }}</h3>
        <p class="status-title"></p>
        <p class="status-number">
          {{ nonListedCompanyCount }} 
        </p>
        <div class="status-note"></div>
      </div>

      <div class="status-box clickable" @click="handleStatusBoxClick('')">
        <h3>202503</h3>
        <p class="status-title"></p>
        <p class="status-number">
          {{ financeCompanyCount }} 
        </p>
        <div class="status-note"></div>
        </div>
    </div>

    <div class="table-header-container">
      <div class="table-header">
        <i class="fas fa-list"></i> <span></span>
      </div>
    </div>

    <div class="grid-container">
      <div class="grid-item">
        <div class="table-wrapper">
          <ChartSpinner :visible="isFirstTableLoading" />
          <table>
            <tr><th colspan="2"></th></tr>
            <tr v-for="(company, index) in pagedListedCompanies" :key="index">
              <td v-text="company.company_name"></td>
            </tr>
            <tr v-if="firstTablePages > 1">
              <td colspan="2">
                <div class="pagination">
                  <button :disabled="firstTablePage === 1" @click="changePage('firstTable', firstTablePage - 1)"></button>
                  <span>{{ firstTablePage }} / {{ firstTablePages }}</span>
                  <button :disabled="firstTablePage === firstTablePages" @click="changePage('firstTable', firstTablePage + 1)"></button>
                </div>
              </td>
            </tr>
          </table>
        </div>
      </div>

      <div class="grid-item">
        <div class="table-wrapper">
          <ChartSpinner :visible="isSecondTableLoading" />
          <table>
            <tr><th colspan="2"></th></tr>
            <tr v-for="(company, index) in pagedNonListedCompanies" :key="index">
              <td v-text="company.company_name"></td>
            </tr>
            <tr v-if="secondTablePages > 1">
              <td colspan="2">
                <div class="pagination">
                  <button :disabled="secondTablePage === 1" @click="changePage('secondTable', secondTablePage - 1)"></button>
                  <span>{{ secondTablePage }} / {{ secondTablePages }}</span>
                  <button :disabled="secondTablePage === secondTablePages" @click="changePage('secondTable', secondTablePage + 1)"></button>
                </div>
              </td>
            </tr>
          </table>
        </div>
      </div>

      <div class="grid-item">
        <div class="table-wrapper">
          <ChartSpinner :visible="isThirdTableLoading" />
          <table>
            <tr><th colspan="2"></th></tr>
            <tr v-for="(company, index) in pagedFinancingCompanies" :key="index">
              <td>{{ company.name }}</td> </tr>
            <tr v-if="thirdTablePages > 1">
              <td colspan="2">
                <div class="pagination">
                  <button :disabled="thirdTablePage === 1" @click="changePage('thirdTable', thirdTablePage - 1)"></button>
                  <span>{{ thirdTablePage }} / {{ thirdTablePages }}</span>
                  <button :disabled="thirdTablePage === thirdTablePages" @click="changePage('thirdTable', thirdTablePage + 1)"></button>
                </div>
              </td>
            </tr>
          </table>
        </div>
      </div>
    </div>

    <div class="footer">
      <div class="footer-overlay"></div>
      <img class="footer-bg" src="@/assets/footer_image.jpg" alt="Footer Image" />
      <div class="admin">
        <button class="admin-login-button" @click="openLoginModal"></button>
      </div>
      <div class="footer-content"> <div class="footer-links">
          <p>dfcc@saif.sjtu.edu.cn</p>
          <a href="#" @click.prevent="openFeedbackModal">/</a>
        </div>
      </div>
    </div>
